import React from 'react';
import obj from './api2';
export default class Article1 extends React.Component {
  constructor(props) {
    super(props)
    this.state = {
      title1: '',
      about2: '',
      markdown3: '',
      tags4: ''
    }
  }
  title1(e) {
    this.setState({
      title1: e.target.value
    })
  }
  about2(e) {
    this.setState({
      about2: e.target.value
    })
  }
  markdown3(e) {
    this.setState({
      markdown3: e.target.value
    })
  }
  tags4(e) {
    this.setState({
      tags4: e.target.value
    })
  }
  componentDidMount() {

  }
  submit(e) {
    e.preventDefault();

  }
  click() {
    obj({
      method: "post",
      url: '/api/articles',
      params: {
        "article": {
          "title": this.state.title1,
          "description": this.state.about2,
          "body": this.state.markdown3,
          "tagList": [this.state.tags4]
        }
      }
      //1梳理整个项目的思路
      // 2复习到了什么？
      //3遇到了哪些bug
      //4.完善功能
    }).then(res => {
      console.log('1111111111111' + res);
      this.setState({
        title1: '',
        about2: '',
        markdown3: '',
        tags4: '',

      })
    })
  }
  render() {
    return <div>
      <div className="editor-page">
        <div className="container page">
          <div className="row">

            <div className="col-md-10 offset-md-1 col-xs-12">
              <form onSubmit={this.submit.bind(this)}>
                <fieldset>
                  <fieldset className="form-group">
                    <input type="text" className="form-control form-control-lg" placeholder="Article Title" value={this.state.title1} onChange={this.title1.bind(this)} />
                  </fieldset>
                  <fieldset className="form-group">
                    <input type="text" className="form-control" placeholder="What's this article about?" value={this.state.about2} onChange={this.about2.bind(this)} />
                  </fieldset>
                  <fieldset className="form-group">
                    <textarea className="form-control" rows="8" placeholder="Write your article (in markdown) " value={this.state.markdown3} onChange={this.markdown3.bind(this)}></textarea>
                  </fieldset>
                  <fieldset className="form-group">
                    <input type="text" className="form-control" placeholder="Enter tags" value={this.state.tags4} onChange={this.tags4.bind(this)} /><div className="tag-list"></div>
                  </fieldset>
                  <button className="btn btn-lg pull-xs-right btn-primary" type="button" onClick={this.click.bind(this)}>
                    Publish Article
            </button>
                </fieldset>
              </form>
            </div>

          </div>
        </div>
      </div>
    </div>
  }
}