import React from 'react';
// import axios from 'axios'
import { addFavorite, deleFavorite } from "./favorite";
import moment from 'moment'
const { obj } = require('./api')
export default class Home extends React.Component {
  constructor(props) {
    super(props)
    this.state = {
      list: {
        articles: [],
      },

      tags: [],
      columepage: 0,
      rows: 10,
      articlesCount: 0,
      pagestate: 1,
      favoritedisabled: false
    }
    this.componentDidMount = this.componentDidMount.bind(this)
  }
  rows(item, e) {
    e.preventDefault();
    // console.log(item);
    obj({
      method: "get",
      url: '/api/articles',
      params: {
        limit: 10,
        offset: (item - 1) * 10
      }
    }).then(res => {
      // console.log(res);
      // console.log(res.data.articlesCount);

      this.setState({
        list: res.data,
        pagestate: item
      })
      // console.log(item);
      // console.log(this.state.pagestate);
    })
  }
  async favorite(data) {
    // console.log(data.favorited);//当前点击的单个文章
    await this.setState({   //异步
      favoritedisabled: true
    })
    if (data.favorited) {
      //取消点赞的业务
      await deleFavorite(data).then((res) => { //异步
        // console.log("取消", res);
      })
      data.favorited = false
      data.favoritesCount += -1;

      // console.log(this.state.data.articles.indexOf(data));
      let key = this.state.list.articles.indexOf(data);
      this.state.list.articles[key]
        = data;
      // console.log(this.state.list.articles[key]);
      // console.log(data);
      await this.setState({  //异步
        articles: this.state.list.articles
      })
    }
    else {
      //点赞的业务
      await addFavorite(data).then((res) => {  //异步
        // console.log("添加", res);
      })
      data.favorited = true
      data.favoritesCount += 1;
      // console.log(this.state.list.articles);
      let key = this.state.list.articles.indexOf(data);
      this.state.list.articles[key] = data;
      await this.setState({  //异步
        articles: this.state.list.articles
      })
    }
    await this.setState({
      favoritedisabled: false
    })
  }
  componentDidMount() {
    // console.log("11111111111111" + this.list);
    // const that = this
    obj({
      method: "get",
      url: '/api/articles',
    }).then(res => {
      console.log(res);
      // console.log(res.data.articlesCount);

      this.setState({
        list: res.data,
        articlesCount: res.data.articlesCount / 10
      })
      // console.log(this.state.articlesCount);
    })

    //标签
    obj({
      method: "get",
      url: '/api/tags?limit=10',
    }).then(res => {
      // console.log(res);
      this.setState({
        tags: res.data.tags
      })
      // console.log(this.state.tags);
    })
  }
  render() {
    // console.log(this.state.list.articles);
    // console.log(this.state.tags); 
    // console.log(this.state.list);
    return <div>
      <div className="home-page">

        <div className="banner">
          <div className="container">
            <h1 className="logo-font">conduit</h1>
            <p>A place to share your knowledge.</p>
          </div>
        </div>

        <div className="container page">
          <div className="row">

            <div className="col-md-9">
              <div className="feed-toggle">
                <ul className="nav nav-pills outline-active">
                  <li className="nav-item">
                    <a className="nav-link disabled" href="">Your Feed</a>
                  </li>
                  <li className="nav-item">
                    <a className="nav-link active" href="">Global Feed</a>
                  </li>
                </ul>
              </div>

              {/* <div className="article-preview">
                <div className="article-meta">
                  <a href="profile.html"><img src="http://i.imgur.com/Qr71crq.jpg" /></a>
                  <div className="info">
                    <a href="/crctle" className="author">Eric Simons</a>
                    <span className="date">January 20th</span>
                  </div>
                  <button className="btn btn-outline-primary btn-sm pull-xs-right">
                    <i className="ion-heart"></i> 29
            </button>
                </div>
                <a href="/crctle" className="preview-link">
                  <h1>How to build webapps that scale</h1>
                  <p>This is the description for the post.</p>
                  <span>Read more...</span>
                </a>
              </div> */}

              <div className="article-preview">
                {this.state.list.articles.map((item, i) => {
                  return <div className="article-meta" key={i}>
                    <img src={item.author.image} />
                    <div className="info">
                      <a href="" className="author">{item.author.username}</a>
                      <span className="date">{moment(item.createdAt).format('YYYY-MM-DD')}</span>
                    </div>
                    <button className="btn btn-outline-primary btn-sm pull-xs-right" onClick={this.favorite.bind(this, item)} disabled={this.state.favoritedisabled}>
                      <i className="ion-heart"></i> {item.favoritesCount}
                    </button>
                    <a href="/crctle" className="preview-link">
                      <h1>{item.title}</h1>
                      <p>{item.body}</p>
                      <span>Read more...</span>
                    </a>
                  </div>
                })}


                {/* </div> */}
              </div>

            </div>

            <div className="col-md-3">
              <div className="sidebar">
                <p>Popular Tags</p>

                <div className="tag-list">
                  {this.state.tags.map((item, i) => {
                    return <a href="" key={i} className="tag-pill tag-default">{item}</a>
                  })}
                  {/* <a href="" className="tag-pill tag-default">programming</a>
                  <a href="" className="tag-pill tag-default">javascript</a>
                  <a href="" className="tag-pill tag-default">emberjs</a>
                  <a href="" className="tag-pill tag-default">angularjs</a>
                  <a href="" className="tag-pill tag-default">react</a>
                  <a href="" className="tag-pill tag-default">mean</a>
                  <a href="" className="tag-pill tag-default">node</a>
                  <a href="" className="tag-pill tag-default">rails</a> */}
                </div>
              </div>
            </div>

          </div>
          <ul className="pagination">



            {Array.from({ length: this.state.articlesCount }, (v, k) => k + 1).map((item, i) => {
              return <li key={i} className={this.state.pagestate == item ? "page-item active" : "page-item"} >
                <a className="page-link " href="" onClick={this.rows.bind(this, item)}>{item}</ a>
              </li>
            })

            }
          </ul>
        </div>

      </div>
    </div >
  }
}