import React from 'react';
import Cookie from 'js-cookie';
import Pubsub from 'pubsub-js';
const { obj } = require('./api')
class A extends React.Component {
  constructor(props) {
    super(props)
    this.blur = this.blur.bind(this)
  }
  blur(event) {
    var value = event.target.value
    Pubsub.publish('value', value)
  }
  render() {
    return (
      <div>
        <fieldset className="form-group">
          <input
            className="form-control form-control-lg"
            type="text"
            placeholder="Your Name"
            onBlur={this.blur}
          />
        </fieldset>
      </div>
    );
  }
}
export default class Login extends React.Component {
  constructor(props) {
    super(props)
    this.state = {
      username: "",
      email: '2713677270@qq.com',
      password: "123123"
    }
    this.email = this.email.bind(this)
    this.password = this.password.bind(this)

  }
  componentDidMount() {
    // obj({
    //   method: 'post',
    //   url: '/api/users/login',
    //   data: {
    //     'user': {
    //       "email": "2713677270@qq.com",
    //       'password': '123123123'
    //     }
    //   }

    // }).then(res => { console.log(res, 'res') })
    // console.log(111111111111111111);
    Pubsub.subscribe("value", (msg, data) => {
      this.setState({
        username: data
      })
    })

  }
  islogin() {
    return this.props.match.path === "/login";
    // return true
  }
  email(e) {
    this.setState({
      email: e.target.value
    })
    // console.log();
  }
  password(e) {
    this.setState({
      password: e.target.value
    })
  }
  tijiao(e) {
    e.preventDefault();
    let user = {
      username: this.state.username ? this.state.username : "",
      "email": this.state.email,
      'password': this.state.password
    }
    obj({
      method: 'post',
      url: this.state.username ? '/api/users' : '/api/users/login',
      data: {
        user: user
      }

    }).then(res => {
      console.log(res, 'res')
      console.log(res.data.user.token);
      Cookie.set('auth', res.data.user.token);
      this.props.history.push('/')
      Pubsub.publish('auth', res.data.user.token);
      // console.log(111111111111111);
    })
  }
  render() {
    console.log(this.state);
    return <div>
      <div className="auth-page">
        <div className="container page">
          <div className="row">

            <div className="col-md-6 offset-md-3 col-xs-12">
              <h1 className="text-xs-center">
                {this.islogin() ? "sign in" : "sign up"}
              </h1>
              <p className="text-xs-center">
                <a href="">
                  {this.islogin() ? "Need an account" : " Have an account"}
                </a>
              </p>

              <ul className="error-messages">
                <li>That email is already taken</li>
              </ul>

              <form onSubmit={this.tijiao.bind(this)}>
                {this.islogin() ? "" : <A />}
                <fieldset className="form-group">
                  <input className="form-control form-control-lg" type="text" placeholder="Email" onBlur={this.email} />
                </fieldset>
                <fieldset className="form-group">
                  <input className="form-control form-control-lg" type="password" placeholder="Password" onBlur={this.password} />
                </fieldset>
                <button className="btn btn-lg btn-primary pull-xs-right">
                  {this.islogin() ? " Sign in" : "  Sign up"}
                </button>
              </form>
            </div>

          </div>
        </div>
      </div>
    </div>
  }
}