import React from 'react'
import { HashRouter as Router, Switch, Route } from "react-router-dom";
import Header from './component/Header/Header';
import Footer from './component/Footer/Footer';
import './main.css'
import Home from './component/Home';
import Arctle from './component/Article';
import Arctle1 from './component/Article1';
import Login from './component/Login'
import Profile from './component/Profile'
import Settings from './component/Settings';
import Cookie from "js-cookie";
class App extends React.Component {
  render() {
    if (Cookie.get('auth')) {
      return <div>
        <Router>
          <Header />
          <Footer />
          <Switch >
            <Route exact path="/" component={Home} />
            <Route exact path="/crctle" component={Arctle} />
            <Route exact path="/login" component={Home} />
            <Route exact path="/register" component={Home} />
            <Route exact path="/settings" component={Settings} />
            <Route exact path="/profile" component={Profile} />
            <Route exact path="/editor" component={Arctle1} />
          </Switch>
        </Router>
      </div>
    } else {
      return <div>
        <Router>
          <Header />
          <Footer />
          <Switch >
            <Route exact path="/" component={Home} />
            <Route exact path="/crctle" component={Arctle} />
            <Route exact path="/login" component={Login} />
            <Route exact path="/register" component={Login} />
            <Route exact path="/settings" component={Home} />
            <Route exact path="/profile" component={Home} />
            <Route exact path="/editor" component={Home} />
          </Switch>
        </Router>
      </div>
    }

  }
}
export default App;
